    hashtags: ['司法特考'],
  }];

  created() {
    if (this.$route.query.tag) this.tagSelected = JSON.parse(JSON.stringify(this.$route.query.tag));
  }

  link() {
    if (this.tagSelected === '') this.$router.push('/main');
    else this.$router.push('/main/news_content');
  }

  show() {
    this.$router.push('/main/news_content');
  }

  search() {
    this.isSearch = true;
  }

  cancel() {
    this.isSearch = false;
  }

  get filterList() {
    return this.isExpandSub ? this.listSub : this.listAll;
  }

  get filterNewsList() {
    if (this.tagSelected === '') return this.listArticle;
    return this.listArticle.filter((d) => d.hashtags.indexOf(this.tagSelected));
  }

  get sliceList() {
    return this.filterNewsList.slice((this.pageIdx - 1) * this.pageSplitNum, this.pageIdx * this.pageSplitNum);
  }

  get pages() {
    return Math.ceil(this.filterNewsList.length / this.pageSplitNum);
  }

  expandSub() {
    if (this.isExpandAll) this.isExpandAll = false;
    this.isExpandSub = !this.isExpandSub;
  }

  expandAll() {
    if (this.isExpandSub) this.isExpandSub = false;
    this.isExpandAll = !this.isExpandAll;
  }

  changeIdx(i: number) {
    this.pageIdx = i;
  }
}
